// in app/admin/page.tsx
import { createClient } from '@/utils/supabase/server';
import { redirect } from 'next/navigation';
import { AdminSettingsClient } from './admin-client-component';

export default async function AdminPage() {
  const supabase = createClient();

  const { data: { user } } = await supabase.auth.getUser();
  if (!user) {
    return redirect('/login');
  }

  // Server-side check for admin privileges
  const { data: profile } = await supabase
    .from('profiles')
    .select('roles(can_manage_all_rosters)')
    .eq('id', user.id)
    .single();

  const isSiteAdmin = (profile?.roles as any)?.can_manage_all_rosters || false;

  // If not a site admin, redirect to the dashboard.
  if (!isSiteAdmin) {
    return redirect('/');
  }

  // Pass the user to the client component
  return <AdminSettingsClient user={user} />;
}